import React, { useState, useEffect } from 'react';
import axios from 'axios';

import { serverIP } from '../config';

import '../styles/global/main.scss';
import '../styles/card/main.scss';

interface lesson {
    name: string,
    groups: {
        w: number,
        c: number,
        l: number
    }
}

interface lessonHour {
    rowStart: number,
    rowEnd: number,
    lessons: Array<lesson>
}

interface lessonDay {
    rowStart: number,
    rowEnd: number,
    '8:00-10:30': lessonHour,
    '10:45-13:15': lessonHour,
    '14:00-16:30': lessonHour,
    '16:45-19:15': lessonHour,
    date: string
}

interface lessonWeek {
    sobota: lessonDay,
    niedziela: lessonDay
}

const Index: React.FC = () => {
    const [lessons, setLessons] = useState([]);
    const [groups, setGroups] = useState({
        w: 1,
        c: 1,
        l: 1
    });

    useEffect(() => {

    }, [groups])

    useEffect(() => {
        axios.get(`${serverIP}/lessons`)
        .then(res => {
            if(res.status === 200) setLessons(res.data);
        })
        .catch(err => console.log(err))
    }, [])

    const changeGroups = (group: string, value: number) => {
        setGroups({...groups, [group]: value})
    }

    return(
        <div className="container container_full">
            <div className="row">
                <div className="col_grow mb-4">
                    <h1 className="h1 color_white">
                        Grupy
                    </h1>

                    <div className="card card_bg-4">
                        <div className="row row_vcenter mb-2">
                            <p className="size_xl color_light col_grow">
                                Ćwiczenia
                            </p>

                            <select 
                                className="size_xl"
                                value={groups.c}
                                onChange={(e) => changeGroups("c", Number(e.target.value))}
                            >
                                <option value="1">1</option>
                                <option value="2">2</option>
                                <option value="3">3</option>
                            </select>
                        </div>

                        <div className="row row_vcenter">
                            <p className="size_xl color_light col_grow">
                                Laborki
                            </p>

                            <select 
                                className="size_xl"
                                value={groups.l}
                                onChange={(e) => changeGroups("l", Number(e.target.value))}
                            >
                                <option value="1">1</option>
                                <option value="2">2</option>
                                <option value="3">3</option>
                                <option value="4">4</option>
                                <option value="5">5</option>
                                <option value="6">6</option>
                            </select>
                        </div>
                    </div>
                </div>
                <div className="col_grow">
                    <table className="table">
                        <thead>
                            <tr>
                                <th>
                                    <p className="size_xl color_light">
                                        Dni
                                    </p>
                                </th>
                                <th>    
                                    <p className="size_xl color_light">
                                        8:00-10:30
                                    </p>
                                </th>
                                <th>    
                                    <p className="size_xl color_light">
                                        10:45-13:15
                                    </p>
                                </th>
                                <th>    
                                    <p className="size_xl color_light">
                                        14:00-16:30
                                    </p>
                                </th>
                                <th>    
                                    <p className="size_xl color_light">
                                        16:45-19:15
                                    </p>
                                </th>
                            </tr>
                        </thead>

                        <tbody className="color_white">
                            {lessons.map((lessonWeek: lessonWeek) => {
                                return Object.keys(lessonWeek).map((lessonDayKey, lessonDayIndex: number) => {
                                    const key: any = lessonDayKey;
                                    const _key: "sobota" | "niedziela" = key;
                                    return (
                                        <tr>
                                            <td className="border_top">
                                                <p>{lessonWeek[_key].date}</p>
                                            </td>

                                            {Object.keys(lessonWeek[_key]).map((lessonHour) => {
                                                if(typeof lessonWeek[_key][lessonHour] === "object"){
                                                    if(lessonWeek[_key][lessonHour].lessons.length > 0){
                                                        return (
                                                            <td className="border_top">
                                                            {lessonWeek[_key][lessonHour].lessons.map((lesson: lesson, lessonIndex: number) => {
                                                            if(lesson.name.includes('wykład')){
                                                                if(lessonIndex > 0){
                                                                    if(lesson.name !== lessonWeek[_key][lessonHour].lessons[lessonIndex - 1].name){
                                                                        return (
                                                                            <div className="card card_bg-color-1 card-lesson">
                                                                                {lesson.name}
                                                                            </div>
                                                                        )
                                                                    }
                                                                }else{
                                                                    return (
                                                                        <div className="card card_bg-color-1 card-lesson">
                                                                            {lesson.name}
                                                                        </div>
                                                                    )
                                                                }
                                                            }else if(lesson.name.includes('ćwiczenia')){
                                                                if(lesson.groups.c === groups.c){
                                                                    if(lessonIndex > 0){
                                                                        if(lesson.name !== lessonWeek[_key][lessonHour].lessons[lessonIndex - 1].name){
                                                                            return (
                                                                                <div className="card card_bg-color-2 card-lesson">
                                                                                    {lesson.name}
                                                                                </div>
                                                                            )
                                                                        }
                                                                    }else{
                                                                        return (
                                                                            <div className="card card_bg-color-2 card-lesson">
                                                                                {lesson.name}
                                                                            </div>
                                                                        )
                                                                    }
                                                                }else{
                                                                    return(<td></td>)
                                                                }
                                                            }else if(lesson.name.includes('lab')){
                                                                if(lesson.groups.l === groups.l){
                                                                    if(lessonIndex > 0){
                                                                        if(lesson.name !== lessonWeek[_key][lessonHour].lessons[lessonIndex - 1].name){
                                                                            return (
                                                                                <div className="card card_bg-color-3 card-lesson">
                                                                                    {lesson.name}
                                                                                </div>
                                                                            )
                                                                        }
                                                                    }else{
                                                                        return (
                                                                            <div className="card card_bg-color-3 card-lesson">
                                                                                {lesson.name}
                                                                            </div>
                                                                        )
                                                                    }
                                                                }else{
                                                                    return(<td></td>)
                                                                }
                                                            }else{
                                                                if(lessonIndex > 0){
                                                                    if(lesson.name !== lessonWeek[_key][lessonHour].lessons[lessonIndex - 1].name){
                                                                        return (
                                                                            <div className="card card_bg-color-4 card-lesson">
                                                                                {lesson.name}
                                                                            </div>
                                                                        )
                                                                    }
                                                                }else{
                                                                    return (
                                                                        <div className="card card_bg-color-4 card-lesson">
                                                                            {lesson.name}
                                                                        </div>
                                                                    )
                                                                }
                                                            }
                                                        })}
                                                        </td>)
                                                    }else{
                                                        return(
                                                            <td className="border_top">
                                                                <></>
                                                            </td>
                                                        )
                                                    }
                                                }
                                            })}
                                        </tr>
                                    )
                                })
                            })}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

export default Index;